@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300;800&display=swap);
body {
  background-color: rgba(0, 0, 0, 0.1);
}

.App {
  font-family: sans-serif;
  text-align: center;
  max-width: 300px;
  margin: 0 auto;
  background: white;
  font-family: "Barlow", sans-serif;
}

.TopWrapper {
  background-color: #4b4989;
  padding: 1em;
}

.Toggles {
  margin-top: 1.5em;
}

.Toggles input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.Toggles label {
  display: inline-block;
  background-color: #312e52;
  padding: 5px 10px;
  font-size: 0.9em;
  font-weight: 800;
  text-transform: uppercase;
  color: #8b6cb5;
}

.Toggles input[type="radio"]:checked + label {
  background-color: white;
  color: #4b4989;
}

.WhiteCard {
  padding: 2em 1em;
  background-color: white;
  border-radius: 1em;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  top: 2.5em;
}

.Result p {
  text-transform: uppercase;
  font-weight: 800;
  color: #4b4989;
  margin: 0;
}

.Result h1 {
  font-weight: 400;
  font-size: 4em;
  margin: 0.2em;
  overflow: hidden;
}

.InputsWrapper {
  padding: 1em;
  margin-top: 2em;
}

.InputsBox {
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.1);
  border-radius: 1em;
  margin: 1em 0;
}

.InputsBox label {
  text-transform: uppercase;
  font-weight: bold;
  color: #4b4989;
}

.InputsLabel {
  padding: 1em 1em 1em 1.6em;
}

.InputsLabel p {
  margin: 0;
}

.InputsBox input {
  border: 0;
  background: #4b4989;
  font-weight: 400;
  font-size: 1.2em;
  color: white;
  text-align: right;
  padding: 1em 2em 1em 1em;
  overflow: hidden;
  border-radius: 0em 1em 1em 0;
}

.InputsBox input:focus {
  background: #312e52;
  border: none;
}

